@import url(https://fonts.googleapis.com/css?family=Coiny|Open+Sans&display=swap);
html,
body {
 margin: 0;
 padding: 0;
 min-height: 100%;
}

a {
 text-decoration: none;
}

:root {
 --bg-sub: #fff8f0;
 --highlight: #6a00f5;
 --color-highlights: #555;
 --color-highlights-bright: #f4acb7;
 --text-color-a: #24313f;
 --text-correct: limegreen;
 --text-shadow-main: 1px 2px 1px #000;
 --box-shadow: 0px 10px 13px -7px #000;
 --dis-highligh: #aaa;
 --dis-bg: #666;
}
body {
 height: 100%;
 background-color: #fff8f0;
 background-color: var(--bg-sub);
}
.app {
 display: flex;
 flex-direction: column;
 align-items: center;
 text-align: center;
}

/* NAVIGATION BAR */
.navbar {
 display: flex;
 flex-wrap: wrap;
 padding: 10px;
}
.logo {
 justify-self: flex-start;
 padding: 0 20px 0 15px;
 background: #6a00f5;
 background: var(--highlight);
 font: 44px "Open-sans", sans-serif;
 font-weight: 900;
 letter-spacing: -4px;
 color: #fff8f0;
 color: var(--bg-sub);
}
.linkContainer {
 padding: 0 20px;
 flex-grow: 1;
 display: flex;
 align-items: center;
 justify-content: center;
}

.link {
 padding: 9px;
 color: #24313f;
 color: var(--text-color-a);
 white-space: nowrap;
 margin-right: 20px;
 font: 18px "Open Sans", serif;
}

.link:hover {
 color: #555;
 color: var(--color-highlights);
}

.linkSelected {
 border: 1px solid #24313f;
 border: 1px solid var(--text-color-a);
 border-radius: 4%;
}

/* GAME CONTAINER */

.gameWrapper,
.textContainer {
 padding: 20px;
 width: 90%;
 height: 70%;
 background: #eee;
 box-shadow: 0px 10px 13px -7px #000;
 box-shadow: var(--box-shadow);
}

.gameWrapper {
 display: flex;
 flex-direction: column;
}

/* STATUS CONTAINER  */
.statusContainer {
 display: flex;
 flex-wrap: nowrap;
 justify-content: center;
 align-items: center;
}

.statusBoard {
 margin: 20px;
}

.statusBoard > span {
 color: #24313f;
 color: var(--text-color-a);
 font: 18px "Open Sans", serif;
}
.scoreBoard {
 justify-self: flex-end;
 flex: 1 0;
}

.scoreBoard > span {
 padding: 10px;
 font: 18px "Open Sans", serif;
 border: 1px solid #24313f;
 border: 1px solid var(--text-color-a);
 border-radius: 4%;
}

/* BTNS */

.userBtns {
 display: block;
 margin: 30px 0;
}

.userBtns > button {
 background-color: #6a00f5;
 background-color: var(--highlight);
 color: #fff8f0;
 color: var(--bg-sub);
 border: none;
 padding: 15px 50px;
 margin: 0 20px;
 border-radius: 40px;
 font-size: 25px;
 font-weight: 600;
 cursor: pointer;
}
.userBtns > button:hover {
 background-color: #555;
 background-color: var(--color-highlights);
}

.userBtns > button > button:disabled {
 background-color: #f4acb7;
 background-color: var(--color-highlights-bright);
}
/*  MUSICAL BLOCKS */

.musicBlockBoard {
 display: flex;
 flex-wrap: wrap;
 justify-content: space-around;
 align-items: center;
}

.musicBlock,
.musicBlockActive {
 margin: 30px 0;
 background: #fff8f0;
 background: var(--bg-sub);
 border-radius: 10px;
 width: 13%;
 height: 75px;
 max-width: 100px;
 display: flex;
 flex-direction: column;
 justify-content: center;
}

.musicBlock {
 color: #24313f;
 color: var(--text-color-a);
 border: 15px solid #fff8f0;
 border: 15px solid var(--bg-sub);
 box-shadow: 0px 10px 13px -7px #000;
 box-shadow: var(--box-shadow);
}

.musicBlock:hover {
 border: 15px solid #555;
 border: 15px solid var(--color-highlights);
 background-color: #555;
 background-color: var(--color-highlights);
 color: #fff8f0;
 color: var(--bg-sub);
 cursor: pointer;
}

.musicBlockActive {
 border: 15px solid #f4acb7;
 border: 15px solid var(--color-highlights-bright);
 box-shadow: 0px 10px 13px -7px #000;
 box-shadow: var(--box-shadow);
}
.musicBlock > span,
.musicBlockActive > span {
 font: bold 30px serif;
}

.mysteryBlock {
 color: limegreen;
 color: var(--text-correct);
}

/* ANSWER BOARD */
.answerBoard {
 display: flex;
 flex-wrap: wrap;
 justify-content: center;
}

.answerBoard > button {
 border: none;
 padding: 10px 20px;
 margin: 10px 4px;
 background-color: #6a00f5;
 background-color: var(--highlight);
 color: #fff8f0;
 color: var(--bg-sub);
 border-radius: 4%;
 font: bold 25px "Open Sans", serif;
 cursor: pointer;
}

.answerBoard > button:disabled {
 background-color: #666;
 background-color: var(--dis-bg);
 color: #aaa;
 color: var(--dis-highligh);
 pointer-events: none;
}
.answerBoard > button:hover {
 background-color: #555;
 background-color: var(--color-highlights);
}
/* TEXT CONTAINER */

.textContainer > h3 {
 text-align: left;
 font: bold 20px "Open Sans", serif;
}

.textContainer > p {
 text-align: left;
 font: 14px "Open Sans", serif;
}

@media (max-width: 600px) {
 .app {
  display: flex;
  justify-content: center;
  align-items: center;
 }
 .navbar {
  flex-direction: column;
  width: 100%;
  margin: 0;
  padding: 0;
 }
 .logo {
  width: 100%;
  padding: 0;
  margin: 0;
 }
 .linkContainer {
  padding: 10px 0;
 }
 .gameWrapper {
  width: 100%;
  margin: 0;
  padding: 0;
 }
 .scoreBoard {
  margin: 10px;
 }
 .userBtns {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
 }
 .userBtns > button {
  font-size: 25px;
  padding: 5px 20px;
  height: 80px;
  width: 50%;
  font-weight: 300;
 }
 .musicBlock,
 .musicBlockActive {
  margin: 10px 4px;
  background: #fff8f0;
  background: var(--bg-sub);
  border-radius: 10px;
  width: 40px;
  height: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
 }
}

